.app-wrapper {
    margin: 0 auto;
    display: grid;
    width: 1200px;

    grid-template-areas:
    'h h'
    'n c';

    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 5px;

    color: white;

}

.app-wrapper-content{

    background-color: #864eb9;
    grid-area: c;

}
.app-wrapper-content img {
    max-width: 100%;
}

